<template>
  <div>
    <div class="linkmap">{{linkMap}}</div>
    <el-card class="box-card">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="合同名称">
          <el-input v-model="form.name" placeholder="请输入合同名称"></el-input>
        </el-form-item>
        <el-form-item label="甲方">
          <el-input v-model="form.firstParty" placeholder="请输入甲方钱包地址" disabled></el-input>
        </el-form-item>
        <el-form-item label="乙方">
          <el-input v-model="form.partyB" placeholder="请输入乙方钱包地址"></el-input>
        </el-form-item>
        <el-form-item label="合同正文">
          <el-input type="textarea" v-model="form.content" class="textarea" size="small"></el-input>
        </el-form-item>
        <el-form-item class="btn-field">
          <el-button v-if="form.partyB == $store.state.address && form.status == 1" type="primary" @click="onConfirm">签署合同</el-button>
          <el-button v-if="form.firstParty == $store.state.address && form.status == 1" type="primary" @click="onRevoke">撤回合同</el-button>
          <el-button v-if="form.firstParty == $store.state.address && form.status == 0" type="primary" @click="onSubmit(1)">发送合同</el-button>
          <el-button v-if="form.firstParty == $store.state.address && form.status == 0" @click="onSubmit(0)">保存草稿</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
  import { addAndModify } from '@/api'
  export default {
    data () {
      return {
        id: -1,
        account: null,
        form: {
          name: '',
          firstParty: '',
          partyB: '',
          content: '',
          status: 0
        }
      }
    },
    mounted () {
      if (this.$route.path.indexOf('add') < 0) {
        this.id = this.$route.params.id
        this.form = this.$store.state.contractList[this.id]
      } else {
        this.form.firstParty = this.$store.state.address
      }
    },
    methods: {
      handleAddAndModify (status) {
        const data = [
          this.id,
          JSON.stringify({
            name: this.form.name,
            firstParty: this.$store.state.address,
            partyB: this.form.partyB,
            content: this.form.content,
            status
          })
        ]
        addAndModify(data).then(res => {
          if (res.code === 0) {
            this.$message.success('提交成功，一分钟后查看交易状态')
          }
        })
      },
      onConfirm () {
        this.$confirm('签署后合同将生效, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.$request('call', 'confirm', [this.id]).then(res => {
            console.log('确认：', res)
            this.$message({
              type: 'success',
              message: '签署成功!'
            })
          })
        })
      },
      onRevoke () {
        this.$request('call', 'revoke', [this.id]).then(res => {
          console.log('撤销：', res)
          this.$message({
            type: 'success',
            message: '撤销成功!'
          })
        })
      },
      onSubmit (status) {
        if (!this.form.name) {
          this.$message.error('请输入合同名称！')
        }
        if (!this.form.partyB) {
          this.$message.error('请输入乙方钱包地址！')
        }
        if (!this.form.content) {
          this.$message.error('请输入合同正文！')
        }
        console.log('submit!')
        this.handleAddAndModify(status)
      }
    }
  }
</script>

<style scoped lang="less">
  .textarea {
    min-height: 240px;
    display: flex;
  }
  .btn-field {
    display: flex;
    justify-content: flex-end;
  }
</style>



